<style scoped>
.cp {
  max-width: 1000px;
  margin: 0 auto;
}

.head {
  line-height: 50px;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  color: cadetblue;
  font-size: 24px;
  font-weight: bold;
  background-color: antiquewhite;
}

.head div:last-child {
  font-size: 16px;
}

.head div:hover {
  cursor: pointer;
  color: cornflowerblue;
}

.main {
  height: calc(100vh - 50px);
  padding-top: 100px;
  font-size: 18px;
  color: #666666;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: beige;
}

.main div {
  margin: 5px 0;
  width: 300px;
}

.main label {
  display: inline-block;
  width: 100px;
  padding: 0 10px;
  text-align: center;
}
</style>
<template>
  <div class="cp">
    <div class="head">
      <div @click="clickHome($event)">首页</div>
      <div @click="clickLogin($event)">返回登录</div>
    </div>
    <div class="main">
      <form action="">
        <h2>用户注册</h2>
        <div>
          <label>用户名:</label>
          <input v-model="userName" type="text" placeholder="请输入用户名">
        </div>
        <div>
          <label>性别:</label>
          <input v-model="gender" type="radio" value="1">男
          <input v-model="gender" type="radio" value="0">女
        </div>
        <div>
          <button @click="submit">提交</button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import {addRegUser} from '@api/api';

export default {
  activated() {

    console.log("register")
  },
  data: () => {
    return {
      userName: "",
      gender: "",
    }
  },
  methods: {
    created() {
      console.log("regis");
    },
    submit() {
      let resp = addRegUser(JSON.stringify(this.$data));
      resp.then(({code, data: {userName, gender}}) => {
        console.log(code, userName, gender);
      });
    },
    clickHome() {
      this.$router.push("/");
    },
    clickLogin() {
      this.$router.push("/login");
    },
  }
}
</script>
